:root {
  --ui-bg: #1a1a1a;
  --ui-fg: #888;
  --ui-bg-lighter: #333;
  --ui-border: #555;
  font-family: Roboto, sans-serif;
}

.view-galaxy, .view-system, .view-missions {
  display: none;
}

.active-galaxy .view-galaxy, .active-system .view-system, .active-missions .view-missions {
  display: revert;
}

.container {
  background-color: var(--ui-bg);
  border: solid 1px var(--ui-border);
  color: var(--ui-fg);
}

.top-bar .container {
  border-top: 0 solid;
}

.bottom-bar .container {
  border-bottom: 0 solid;
}

.right-bar .container {
  border-right: 0 solid;
}

.left-bar .container {
  border-left: 0 solid;
}

.container .title {
  border-bottom: solid 1px var(--ui-border);
  background-color: var(--ui-bg-lighter);
  text-align: center;
  margin-bottom: 8px;
  padding: 6px;
}

.container .body {
  padding: 8px;
}

.vertical-stack {
  flex-direction: column;
  display: flex;
}

.container .field {
  padding: 4px;
}

.btn {
  background-color: var(--ui-bg);
  border: solid 1px var(--ui-border);
  color: #fff;
  cursor: pointer;
  padding: 6px 8px;
  box-shadow: 0 0 5px #000;
}

.btn:hover {
  background-color: var(--ui-bg-lighter);
}

.label-file-picker input[type="file"] {
  display: none;
}

